.ieq-product-slider {
  overflow: hidden;
  .slider-wrapper {
    display: flex;
    align-items: center;
    gap: 80px;
    overflow: scroll;
  }
}
.slider-card {
  max-width: 936px;
  max-height: 560px;
  display: flex;
  align-items: center;
  gap: 58px;
  padding: 24px 24px 24px 64px;
  background: var(--bg-variant1-l);
  border-radius: 40px;
  p {
    margin-top: 24px;
  }

  &__text {
    min-width: 428px;
    width: 100%;
  }

  &__img {
    img {
      border-radius: 20px;
    }
  }
}
